<template>
<div id="addCustomer">
<van-nav-bar
  title="添加客户"
  left-arrow
  class="addCustomer-nav"
   @click-left="leftClick"
/>
	<div class="addCustomer-content">
		<van-cell-group inset >
			<h4>基础信息</h4>
			<!-- 输入任意文本 -->
			<van-field v-model="customerInfo.corporatename" label="公司名称" required maxlength="20" placeholder="请输入客户名称" />
			<van-field v-model="customerInfo.name" label="客户名称" required maxlength="10"  placeholder="请输入客户名称" />
				<van-field name="radio" label="性别">
				<template #input>
					<van-radio-group  v-model="customerInfo.sex	" checked direction="horizontal">
					<van-radio name="0">男<span style="margin-right:20px;"></span></van-radio>
					<van-radio name="1">女</van-radio>
					</van-radio-group>
					</template>
				</van-field>	
			<van-field v-model="customerInfo.website" label="网址"  maxlength="60" placeholder="请输入网址" />
				<van-field name="radio" label="类型">
				<template #input>
					<van-radio-group  v-model="customerInfo.typeid" direction="horizontal">
					<van-radio name="0">客户<span style="margin-right:20px;"></span></van-radio>
					<van-radio name="1">供应商</van-radio>
					</van-radio-group>
					</template>
				</van-field>

			</van-cell-group>
			<van-cell-group inset >
				<h4>联系信息</h4>
				<!-- 输入任意文本 -->
				<van-field v-model="customerInfo.email" label="电子邮件" required maxlength="30"  placeholder="例如：123@qq.com" />
				<van-field v-model="customerInfo.location" label="地址" required maxlength="60"  placeholder="请输入客户名称" />
				<van-field v-model="customerInfo.phone" label="固话"  maxlength="12"  placeholder="请输入固话号码" />
				<van-field v-model="customerInfo.telephone" label="手机号" required maxlength="11"  placeholder="请输入手机号码" />
				<van-field v-model="customerInfo.fax" label="传真" placeholder="例如：  000-000-000" maxlength="12" />
				<van-field v-model="customerInfo.postcode" label="邮政编码" maxlength="8"  required placeholder="例如：526060" />
			</van-cell-group>
			<van-cell-group inset>
				<h4>备注</h4>
				<van-field
					v-model="customerInfo.content"
					rows="2"
					autosize
					type="textarea"
					maxlength="50"
					placeholder="别输入了！ 这里TM输入了也不保存...（ps：只是让TM页面看起来饱满一点）"
					show-word-limit
				/>
			</van-cell-group>
	</div>
	<div class="bottom-box">
		<i class="send-btn" @click="sendClick">保存</i>
	</div>
</div>
	
</template>

<script>
import { NavBar,Field, CellGroup,RadioGroup, Radio} from 'vant'

import {addCustomer} from 'network/customer'

export default {
  name: 'AddCustomer',
	components:{
		[NavBar.name]:NavBar,
		[Field.name]:Field,
		[CellGroup.name]:CellGroup,
		[RadioGroup.name]:RadioGroup,
		[Radio.name]:Radio,
	},
	data(){
		return{
			customerInfo:{
				corporatename:'',		//1.公司名称
				name:'',				//2.名称
				sex:'0',				//3.性别
				website:'',				//4.网址
				typeid:'0',				//5.类型(是客户还是供应商默认客户)
				email:'',				//6.电子邮件
				location:'',			//7.地址
				phone:'',				//8.固话
				telephone:'',			//9.手机号
				fax:'',					//10.传真
				postcode:'',			//11.邮政编码
				content:'',				//11.占位内容
			},
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},

		//保存按钮
		sendClick(){
			//保存前需要各种验证 这里可以写进一个工具类里面
			let info=this.customerInfo							//简便一下-省点字数
			let _self=this
			//各种验证
			if(this.$checkIsNull(info.corporatename)){
				this.$toast('公司名称不能为空')
				return
			}
			if(this.$checkIsNull(info.name)){
				this.$toast('客户名称不能为空')
				return
			}
			if(this.$checkIsNull(info.email)){
				this.$toast('电子邮件不能为空')
				return
			}
			if(this.$checkIsNull(info.location)){
				this.$toast('地址不能为空')
				return
			}
			if(this.$checkIsNull(info.telephone)){
				this.$toast('手机号不能为空')
				return
			}
			if(this.$checkIsNull(info.postcode)){
				this.$toast('邮政编码不能为空')
				return
			}
			//以上都通过了-写入
			//console.log(this.customerInfo)
			addCustomer(this.customerInfo).then(res=>{
				_self.$toast(res)
			})
			
			//返回上一页-清空对象
			this.$router.go(-1)
			//如果成功就清除所有表单按钮并且跳转
			;
		}
	}
}
</script>

